import React, { useEffect, useState } from 'react'
import { Button, Collapse, Spin } from 'antd'
import { connect } from 'react-redux'
import { getDataAction, updateUserAction } from '../../../redux/dataDuck'
import text from '../../text'
import { Link } from 'react-router-dom'
let { Panel } = Collapse

const Faqinfo = ({match, info, dataUpdated, history, fetching, language, getData, updateUser}) => {

    let { params } = match
    let [title, setTitle] = useState('')

    let handleTitle = () => {
        switch(params.module){
            case 'services':
                return setTitle(text.propietaryServices.publicServices[language])
            default: 
                return
        }
    }

    let handleSubmit = typeInput => {

    }

    useEffect(() => {
        getData()
        handleTitle()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div>
                            <div className="app-top-icon">
                                <img src="/img/sidebar/faq.svg" alt="profile"/>
                                <p>{text.sidebar.faq[language]}</p>
                            </div>
                            <p className='subpage-title'>{title}</p>
                        </div>
                        <div className='padded-app-container form-container condensed-inputs'>
                            <div className="accordion-container">
                                <Collapse defaultActiveKey={['1']}>
                                    <Panel header="QUESTION 1" key="1">
                                        <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Aspernatur, tenetur velit. Distinctio possimus veniam dicta error in quas, dolorem est molestias, totam obcaecati ducimus, illum maiores dolorum. Sequi, alias ullam.</p>
                                    </Panel>
                                    <Panel header="QUESTION 2" key="2">
                                        <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. A cupiditate reprehenderit hic, natus at quisquam aliquam cumque commodi nesciunt porro quaerat laboriosam officia repellendus quidem repudiandae eius exercitationem molestias ad.</p>
                                    </Panel>
                                    <Panel header="QUESTION 3" key="3">
                                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Obcaecati, iure eius est, eos ab neque minima exercitationem nostrum ut dicta debitis rerum sequi dolore, error vero consectetur quas corrupti. Minima.</p>
                                    </Panel>
                                </Collapse>
                            </div>
                            <Link to='/faq'><Button className='nav-button'>{text.button.returnCaps[language]}</Button></Link>
                        </div>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching},
    data: {info, dataUpdated}
}) => ({
    language,
    fetching,
    info,
    dataUpdated
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        updateUser: updateUserAction
    }
)(Faqinfo)
